import { getAttribute, reflect, reflectList } from "../analyzers/css-selectors";
import { BookDataSource } from "../BookDataSource";

/**
 * @descriptin 
 */
export interface Chapter {
  // 
  readonly name?: string;
  // 
  readonly url?: string;
}

/** Chapter */
export namespace Chapter {

  /**
   * @description 
   * @extends {Chapter}
   */
  export interface Rule extends Chapter {
    // 
    readonly chapterList?: string;
    // 
    readonly nextPageUrl?: string;
  }

  /**
   * @description 
   * @param {Element} source  
   * @param {Rule} rule 
   * @return {*}  {Chapter}
   */
  export function getChapter(source: Element, rule: Rule): Chapter {
    return reflect(source, rule, ['chapterList', 'nextPageUrl']);
  }

  /**
   * @description 
   * - (`nextPageUrl`)
   * @param {Element} source 
   * @param {Rule} rule 
   * @return {*}  {Chapter[]}
   */
  export function getChapterList(source: Element, rule: Rule): Chapter[] {
    const { chapterList } = rule;
    if(chapterList === undefined) {
      return [];
    }
    return reflectList(source, chapterList, rule, ['chapterList', 'nextPageUrl']);
  }

  /**
   * @description 
   * - 
   * @param {string} chapterListUrl 
   * @param {Rule} rule 
   * @return {*}  {Promise<Chapter[]>}
   */
  export function getChapterListAwait(chapterListUrl: string, rule: Rule): Promise<Chapter[]>;

  /**
   * @description 
   * - 
   * @param {Element} source 
   * @param {Rule} rule 
   * @return {*}  {Promise<Chapter[]>}
   */
  export function getChapterListAwait(source: Element, rule: Rule): Promise<Chapter[]>;

  export function getChapterListAwait(data: Element | string, rule: Rule): Promise<Chapter[]>;
  export function getChapterListAwait(data: Element | string, rule: Rule): Promise<Chapter[]> {
    if(rule.chapterList === undefined) {
      return Promise.resolve([]);
    }

    // 
    if(typeof data === 'string') {
      const result = BookDataSource.request(data).then(source => getChapterListAwait(source, rule));
      return result;
    }

    // 
    const source = data;
    const list = getChapterList(source, rule);
    if(list.length === 0) {
      // 
      return Promise.resolve(list);
    }

    // 
    let { nextPageUrl } = rule;
    nextPageUrl = nextPageUrl === undefined ? '' : getAttribute(source, nextPageUrl);
    if(nextPageUrl.length === 0) {
      return Promise.resolve(list);
    }

    // 
    const result = getChapterListAwait(nextPageUrl, rule).then(nextList => list.concat(nextList));
    return result;
  }
}




/** ------------ Chapter Content -------------- */
/** ------------ Chapter Content -------------- */
/** ------------ Chapter Content -------------- */
/** ------------ Chapter Content -------------- */
/** ------------ Chapter Content -------------- */
export namespace Chapter {

  /**
   * @description 
   */
  export interface ContentRule {
    // 
    readonly content?: string;
    // 
    readonly nextContentUrl?: string;
  }

  /**
   * @description 
   * - 
   * @param {Element} source 
   * @param {ContentRule} rule 
   * @return {*}  {Promise<string>}
   */
  export function getContent(source: Element, rule: ContentRule): string {
    return reflect(source, rule, ['nextContentUrl']).content ?? '';
  }

  /**
   * @description 
   * - , 
   * @param {string} contentUrl 
   * @param {ContentRule} rule  
   * @return {*}  {Promise<string>}
   */
  export function getContentAwait(contentUrl: string, rule: ContentRule): Promise<string>;

  /**
   * @description 
   * - , 
   * @param {Element} source 
   * @param {ContentRule} rule 
   * @return {*}  {Promise<string>}
   */
  export function getContentAwait(source: Element, rule: ContentRule): Promise<string>;

  export function getContentAwait(data: Element | string, rule: ContentRule): Promise<string> {
    // 
    if(typeof data === 'string') {
      const result = BookDataSource.request(data).then(source => getContentAwait(source, rule));
      return result;
    }

    // 
    const source = data;
    const { content, nextContentUrl} = reflect(source, rule);

    // 
    if(nextContentUrl === undefined || !content?.length) {
      // 
      return Promise.resolve(content ?? '');
    }
    const result = getContentAwait(nextContentUrl, rule).then(nextContent => `${content}\n${nextContent}`);
    return result;
  }
}