/***********************************************************
 * @description css 
 * - *************
 * - `||`: 
 * - `&&`: 
 * - `%%`: , : [[1, 2, 3], [4, 5, 6]] => [[1, 4], [2, 5], [3, 6]]
 * - *************
 * - `-`: 
 * - `+`:
 * - *************
 * - `@key####`
 * - `textContent`
 * - `####`: ##
 * - : `@href`,`@src`...
 *  `.class1 div > a@href##/\d/##`
************************************************************/

import Debug from "../../../utils/debug";

/**
 * @description 
 */
export interface Rule {
  // 
  readonly raw: string;
  // 
  readonly kind: GroupKind;
  // 
  readonly segments: RuleSegment[];
}

/**
 * @description 
 */
export interface RuleSegment {
  // css-selector
  readonly selector: string;
  // attribute
  readonly attribute?: string;
  // 
  readonly replaceRegexp?: string;
  // 
  readonly replacement?: string;
  // 
  readonly reverse?: boolean;
}

/**
 * @description 
 */
export enum GroupKind {
  Default = '||',
  Just = '||',
  Merge = '&&',
  Loop = '%%'
}

// kinds
const groupKinds = [
  GroupKind.Just,
  GroupKind.Merge,
  GroupKind.Loop,
];

/**
 * @description 
 * @param {string} ruleText
 * @return {*}  {Rule}
 */
export function analyze(ruleText: string): Rule {
  let kind = groupKinds.find(value => ruleText.includes(value)) ?? GroupKind.Default;
  const segments = ruleText
    .split(kind)
    .map(value => analyzeSegment(value))
    .filter(value => value !== null) as RuleSegment[];
  return {
    raw: ruleText,
    kind,
    segments
  }
}

// selector regexp
const SelectorRegExp = /(\+|\-)?([^@]+)/;

// attribute regexp
const AttributeRegExp = /@([\w]+)/;

// replace regexp
const ReplaceRegExp = /##([^#]+)(?:##([^#]+))?$/;

/**
 * @description 
 * @param {string} ruleText
 * @return {*}  {RuleSegment}
 */
function analyzeSegment(ruleText: string): RuleSegment | null {
  // 
  const selectorResult = SelectorRegExp.exec(ruleText);
  if(selectorResult === null) {
    Debug.error(`![rule=${ruleText}]`);
    return null;
  }
  const [, flag, selector] = selectorResult;
  const reverse = flag === '-';

  // 
  const attribute = AttributeRegExp.exec(ruleText)?.[1];

  // 
  const replaceResult = ReplaceRegExp.exec(ruleText);
  const [, replaceRegexp, replacement] = replaceResult ?? [];

  return {
    selector,
    attribute,
    reverse,
    replaceRegexp,
    replacement
  }
}

