import { getAttribute } from "./selector.attribute";
import { getElements } from "./selector.element";

/**
 * @descripton 
 * @template T `key`
*/
type RuleMap<T extends string> = Readonly<Partial<Record<T, string>>>;

/**
 * @description 
 * @template T
 * @param {Element} source 
 * @param {RuleMap<T>} ruleMap  { :  }
 * @return {*}  {RuleMap<T>}
 */
export function reflect<T extends string>(source: Element, ruleMap: RuleMap<T>): RuleMap<T>;
/**
 * @description 
 * @template T
 * @template U
 * @param {Element} source 
 * @param {RuleMap<T>} ruleMap  { :  }
 * @param {U[]} excludes 
 * @return {*}  {Omit<RuleMap<T>, U>}
 */
export function reflect<
  T extends string, U extends T
>(source: Element, ruleMap: RuleMap<T>, excludes: U[]): Omit<RuleMap<T>, U>;

export function reflect<
  T extends string, U extends T = never
>(source: Element, ruleMap: RuleMap<T>, excludes?: U[]): Omit<RuleMap<T>, U> {
  const _object: { [K in T]?: string } = {};
  let keys = Object.keys(ruleMap) as T[];
  if(excludes?.length) {
    keys = keys.filter(key => excludes.includes(key as U) === false);
  }
  keys.forEach(key => {
    const rule = ruleMap[key];
    if(rule !== undefined) {
      const value = getAttribute(source, rule);
      _object[key] = value;
    }
  });
  return _object as RuleMap<T>;
}

/**
 * @description `listRule``Element`
 * @template T
 * @param {Element} source 
 * @param {string} listRule `Element`
 * @param {RuleMap<T>} ruleObject  { :  }
 * @return {*}  {RuleMap<T>[]}
 */
export function reflectList<T extends string>(source: Element, listRule: string, ruleObject: RuleMap<T>): RuleMap<T>[];

/**
 * @description `listRule``Element`
 * @template T
 * @template U
 * @param {Element} source 
 * @param {string} listRule `Element`
 * @param {RuleMap<T>} ruleMap  { :  }
 * @param {U[]} excludes 
 * @return {*}  {Omit<RuleMap<T>, U>[]}
 */
export function reflectList<
  T extends string, U extends T
>(source: Element, listRule: string, ruleMap: RuleMap<T>, excludes: U[]): Omit<RuleMap<T>, U>[];

export function reflectList<
  T extends string, U extends T = never
>(source: Element, listRule: string, ruleObject: RuleMap<T>, excludes?: U[]): Omit<RuleMap<T>, U>[] {
  const elements = getElements(source, listRule);
  if(excludes === undefined) {
    return elements.map(element => reflect(element, ruleObject))
  }
  return elements.map(element => reflect(element, ruleObject, excludes));
}