// 
export interface BookSettings {
  // 
  readonly font: number;
  // 
  readonly fontSize: number;
  // 
  readonly lineHeight: number;
  // 
  readonly padding: number;
  // 
  readonly volitStyle: number;
  // 
  readonly background: number;
  //  0-100
  readonly lightting: number;
}

// 
export namespace BookSettings {
  // localStaorage
  const LocalStorageKey = 'leaf.book.booksettings';

  // 
  export const config = {
    font: getFontConfigs(),
    fontSize: getFontSizeConfigs(),
    lineHeight: getLineHeightConfigs(),
    padding: getPaddingConfigs(),
    volitStyle: getVoltiStyleConfigs(),
    background: getBackgroundConfigs(),
    lightting: [0, 100]
  } as const;

  // 
  export const defaultSettings: BookSettings = {
    font: 0,
    fontSize: 2,
    lineHeight: 2,
    padding: 2,
    volitStyle: 0,
    background: 0,
    lightting: 50
  };

  /**
   * @description 
   * @param {Exclude<keyof BookSettings, 'lightting'>} key
   * @param {number} index
   * @return {*}  {string}
   */
  export function get(key: Exclude<keyof BookSettings, 'lightting'>, index: number): string {
    const cfgs = config[key];
    return cfgs[index]?.value ?? '';
  }

  /**
   * @description 
   * @return {*}  {Promise<BookSettings>}
   */
  export function load(): Promise<BookSettings> {
    const data = localStorage.getItem(LocalStorageKey);
    const result = data === null ? defaultSettings : JSON.parse(data);
    return Promise.resolve(result);
  }

  /**
   * @description 
   * @param {BookSettings} settings
   * @return {*}  {Promise<void>}
   */
  export function save(settings: BookSettings): Promise<void> {
    const data = JSON.stringify(settings);
    localStorage.setItem(LocalStorageKey, data);
    return Promise.resolve();
  }














  // ============================
  // ============================
  // ============================
  // ============================
  // 
  type SettingsConfigItem = {name: string, value: string};

  // 
  function getFontConfigs(): readonly SettingsConfigItem[] {
    return [
      { name: '', value: 'Helvetica Neue'},
      { name: '', value: 'Helvetica Neue'},
      { name: '', value: 'Helvetica Neue' },
      { name: '', value: 'Helvetica Neue'},
    ];
  }

  // 
  function getFontSizeConfigs(): readonly SettingsConfigItem[] {
    const cfgs: SettingsConfigItem[] = [];
    for (let value = 16; value < 41; value++) {
      cfgs.push({name: `:${value}`, value: `${value}px`});
    }
    return cfgs;
  }

  // 
  function getLineHeightConfigs(): readonly SettingsConfigItem[] {
    const cfgs: SettingsConfigItem[] = [];
    const initValue = 1;
    const offset = 0.25;
    for (let idx = 0; idx < 7; idx++) {
      const value = `${idx * offset + initValue}rem`;
      cfgs.push({name: `:${idx}`, value});
    }
    return cfgs;
  }

  // 
  function getPaddingConfigs(): readonly SettingsConfigItem[] {
    const cfgs: SettingsConfigItem[] = [];
    const initValue = 0.5;
    const offset = 0.25;
    for (let idx = 0; idx < 7; idx++) {
      const value = `${idx * offset + initValue}rem`;
      cfgs.push({name: `:${idx}`, value});
    }
    return cfgs;
  }

  // 
  function getVoltiStyleConfigs(): readonly SettingsConfigItem[] {
    return [
      { name: '', value: 'read'},
      { name: '', value: 'horizontal'},
      { name: '', value: 'vertical'},
    ];
  }

  // 
  function getBackgroundConfigs(): readonly SettingsConfigItem[] {
    return [
      {name: '', value: '#f5f5f5'},
      {name: '', value: '#FAF9DE'},
      {name: '', value: '#FFF2E2'},
      {name: '', value: '#E3EDCD'},
      {name: '', value: '#DCE2F1'},
      {name: '', value: '#EAEAEF'},
      {name: '', value: 'url(/img/bg1.jpg)'},
      {name: '', value: 'url(/img/bg2.jpg)'},
    ];
  }

}
