import { reflectList } from "../analyzers/css-selectors";
import { BookDataSource } from "../BookDataSource";
import { BookInfo } from "./BookInfo";

/** Search */
export namespace Search {

  /**
   * @description 
   * @extends {BookInfo}
   */
  export interface Rule extends BookInfo {
    // 
    bookList?: string;
  }

  /**
   * @description 
   * @param {Element} source    
   * @param {Rule} rule 
   * @return {*}  {BookInfo[]}
   */
  export function getBookInfoList(source: Element, rule: Rule): BookInfo[] {
    const { bookList } = rule;
    if(bookList === undefined) {
      return [];
    }
    return reflectList(source, bookList, rule, ['bookList']);
  }

  /**
   * @description 
   * @param {string} searchUrl 
   * @param {Rule} rule 
   * @return {*}  {Promise<BookInfo[]>}
   */
  export function getBookInfoListAwait(searchUrl: string, rule: Rule): Promise<BookInfo[]> {
    return BookDataSource.request(searchUrl).then(source => getBookInfoList(source, rule));
  }
}