import { reflect } from "../analyzers/css-selectors";

/**
 * @description 
 */
export interface BookInfo {
  // 
  readonly name?: string;
  // 
  readonly author?: string;
  // 
  readonly intro?: string;
  // 
  readonly kind?: string;
  // 
  readonly lastChapter?: string;
  // 
  readonly updateTime?: string;
  // 
  readonly bookUrl?: string;
  // 
  readonly coverUrl?: string;
  // 
  readonly wordCount?: string;
  // url
  readonly chapteListsUrl?: string;
}

/** BookInfo */
export namespace BookInfo {

  /**
   * @description 
   * @extends {BookInfo}
   */
  export interface Rule extends BookInfo {}

  /**
   * @description 
   * @param {Element} source  
   * @param {Rule} rule   
   * @return {*}  {BookInfo}
   */
  export function getBookInfo(source: Element, rule: Rule): BookInfo {
    const result = reflect(source, rule);
    return result;
  }
}